<template>
    <div class="content-wrap">
        <el-row>
            <el-col :span="18">
                <el-form ref="bookingForm" :rules="rules" :model="form" label-width="100px">
                    <el-form-item label="" prop="receiptFee">
                        <el-input  v-model="form.receiptFee" step="0.1" type="number" style="width:200px;"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="source">
                        <el-select v-model="form.source" placeholder="">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="freshin" value="freshin"></el-option>
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="receiverName">
                        <el-input v-model="form.receiverName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="receiverPhone">
                        <el-input v-model="form.receiverPhone"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="receiverAddress">
                        <el-input v-model="form.receiverAddress"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="memo">
                        <el-input v-model="form.memo"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-button 
                            icon="plus"
                            style="margin-bottom:10px;"
                            @click="dialogTableVisible = true"
                        ></el-button>
                        <el-table :data="orders" style="width:800px;height:">
                            <el-table-column label="" prop="product"></el-table-column>
                            <el-table-column label="" prop="properties"></el-table-column>
                            <el-table-column label="" prop="receiptFee">
                                <template scope="scope">
                                    <el-input v-model="scope.row.receiptFee" step="0.1" type="number"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="" width="">
                                <template scope="scope">
                                    <el-input v-model="scope.row.number" :min="1" step="1" type="number">
                                    </el-input >
                                </template>
                            </el-table-column>
                            <el-table-column label="">
                                <template scope="scope">
                                    <el-button icon="delete" @click="deleteOrder(scope.row)"></el-button>
                                </template>
                            </el-table-column>
                            <div class="line" style="height:2px;width:100%"></div>
                        </el-table>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        
        <el-dialog title="" v-model="dialogTableVisible">
            <product-list :sku-selection="skuSelection"></product-list>
        </el-dialog>
    </div>
</template>

<script>
import productList from './product-list.vue'
export default {
    components: {
        productList
    },
    data() {
        return {
            orders: [],
            dialogTableVisible: false,
            form: {
                receiptFee: '',
                source: "",
                receiverName: '',
                receiverPhone: '',
                receiverAddress: '',
                memo: ''
            },
            rules: {
                receiptFee: [{
                    required: true,
                    message: "",
                    trigger: 'blur'
                }],
                source: [{
                    required: true,
                    message: "",
                    trigger: 'change'
                }],
                receiverName: [{
                    required: true,
                    message: ""
                }],
                receiverPhone: [{
                    required: true,
                    message: ""
                }],
                receiverAddress: [{
                    required: true,
                    message: ""
                }]
            }
        }
    },
    methods: {
        skuSelection(sku) {
            let order = this.orders.find(function(order) {
                return order.id == sku.id;
            });
            if (!order) {
                this.orders.push(sku);
            } else {
                order.number += 1;
            }
        },
        deleteOrder(row) {
            let index = this.orders.indexOf(row);
            this.orders.splice(index, 1);
        },
        onSubmit() {
            if (!this.orders.length) {
                this.$message({
                    message: "",
                    type: 'error'
                });
                return false;
            }
            this.$refs["bookingForm"].validate((valid) => {
                let that = this;
                if (valid) {
                    let orders = this.orders.map(order => {
                        return [order.goodsNo, order.number, order.receiptFee];
                    });
                    this.form.orders = JSON.stringify(orders).replace(/"/g, '\\"');
                    this.$store.dispatch("createBooking", this.form)
                        .then(booking => {
                            that.$message({
                                message: "",
                                type: "success"
                            })
                            location.href = "#/home/booking/list"
                        });
                } else {
                    return false;
                }
            });
        }
    }
}
</script>
<style scoped>
.content-wrap{

    box-sizing: border-box;


}
.line{
    height: 2px;
    width: 100%
}
</style>


