import Vue from 'vue';
import Router from 'vue-router';
import home from '@/components/home';
import center from '@/components/sideNav/center';
import workplat from '@/components/sideNav/workplat';
import bookingNav from '@/components/sideNav/booking';
import login from '@/components/login/login.vue';
import repolist from '../components/content/repos.vue';
import creatRepo from '../components/content/createrepo.vue';
import createuser from '@/components/content/createuser';
import allusers from '../components/content/users.vue';
import brands from '../components/content/brand.vue';
import categories from '../components/content/category.vue';
import createprd from '../components/content/createProduct.vue';
import products from '../components/content/products.vue';
import skus from '../components/content/sku.vue';
import product from '../components/sideNav/product.vue'
import bookingForm from '../components/booking/booking-form.vue';
import creatcats from '../components/content/createcatory.vue'
import bookingList from '../components/booking/booking-list.vue';
import creatCategory from '../components/content/createcatory.vue'
import deliveryNav from '../components/sideNav/delivery.vue'
import deliveryBookingList from "../components/delivery/booking-list.vue"
import intoRepo from '../components/content/createIntoRepo.vue'
import updateProduct from '../components/content/updateProduct.vue'
import inRepos from '../components/content/intoRepos.vue'
import shipDetail from '../components/delivery/shipDetail.vue'
import workStep from '../components/statistics/workstep.vue'
import actionList from '../components/authority/actionList.vue'
import updateAction from '../components/authority/updateAction.vue'
import dToAuth from '../components/login/dToAuth.vue'

const userAction  = resolve => require(['../components/authority/userAction.vue'], resolve)
const createbrand  = resolve => require(['../components/content/createbrand.vue'], resolve)
const updateBrand  = resolve => require(['../components/statistics/upload.vue'], resolve)
const updateSku = resolve => require(['../components/content/updateSku.vue'], resolve)
Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: '',
            component: login 
        },
      

        {
            path: '/home',
            component: home,
            children: [

                {
                    path: 'center',
                    name: '',
                    component: center,
                    children: [
                        {
                            path: 'repolist',
                            name: '',
                            component: repolist
                        },
                        {
                            path: 'creatRepo',
                            name: '',
                            component: creatRepo
                        },
                         {
                            path: 'intoRepo',
                            name: '',
                            component: intoRepo
                        },
                        {
                            path: 'createbrand',
                            name: '',
                            component: createbrand
                        },
                        {
                            path: 'brand',
                            name: '',
                            component: brands
                        },
                        {
                            path:"updateBrand",
                            name:"",
                            component:updateBrand
                        },
                        {
                            path: 'categories',
                            name: '',
                            component: categories
                        },
                         {
                            path: 'creatCategory',
                            name: '',
                            component: creatCategory
                        },
                        
                        {
                            path: 'skus',
                            name: 'Sku',
                            component: skus
                        },
                        
                     
                    ]
                },
                {
                    path:'product',
                    name:'',
                    component:product,
                    children:[
                        {
                            path: 'createprd',
                            name: '',
                            component: createprd
                        },
                        {
                            path: 'products',
                            name: '',
                            component: products
                        },
                        {
                            path:'updateprd',
                            name:'',
                            component:updateProduct
                        },
                     
                        {
                            path:'updateSku',
                            name:'SKU',
                            component:updateSku
                        }
                        
                    ]
                },
                {
                    path: 'workplat',
                    name: '',
                    component: workplat,
                    children: [
                        {
                            path: 'createuser',
                            name: '',
                            component: createuser
                        },
                        {
                            path: 'allusers',
                            name: '',
                            component: allusers
                        },
                        {
                            path: 'userAction',
                            name: '',
                            component: userAction
                        },
                        {
                            path:"actionList",
                            name:"",
                            component:actionList
                        },
                        {
                            path:"update",
                            name:"",
                            component:updateAction
                        }


                    ]
                },
                {
                    path: 'booking',
                    name: "",
                    component: bookingNav,
                    children: [
                        {
                            path: 'create',
                            name: "",
                            component: bookingForm
                        },
                        {
                            path: 'list',
                            name: "",
                            component: bookingList
                        }
                    ]
                },
                {
                    path: 'delivery',
                    name: '',
                    component: deliveryNav,
                    children: [
                        {
                            path: 'booking',
                            name: "",
                            component: deliveryBookingList
                        },
                    
                        {
                            path:"shipDetail",
                            name:"",
                            component:shipDetail
                        }
                    ]
                },
                {
                    path:'inRepos',
                    name:'',
                    component:inRepos
                },
                {
                    path:'',
                    name:'',
                    component:workStep
                },
            ]
        },
        {
            path:'/dToAuth',
            name:"",
            component:dToAuth,

        },

   
    ]
});





